.transactionHistory {
  width: 500px;
  margin: 0 auto;
  text-align: center;
  font-family: sans-serif;
}
.head {
  background-color: rgb(94, 223, 255);
  color: white;
  height: 30px;
  font-family: sans-serif;
}
.list {
  color: rgb(158, 157, 157);
}
.items {
  height: 30px;
  &:nth-child(odd) {
    background-color: rgb(241, 241, 241);
  }
  &:nth-child(even) {
    background-color: rgb(255, 255, 255);
  }
}
